<template>
  <div id="app">
    <WelcomeTip v-if="showWelcome"></WelcomeTip>
    <router-view></router-view>
  </div>
</template>

<script>
import WelcomeTip from "./components/WelcomeTip.vue";

export default {
  name: "App",
  components: {
    WelcomeTip,
  },
  data() {
    return {
      showWelcome: true,
    };
  },
  methods:{
    closeWelcomeTip(){
      this.showWelcome = false
    }
  },
  mounted(){
    this.$bus.$on("closeWelcomeTip",this.closeWelcomeTip)
  }
};
</script>

