import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
    first:true,
    stepsCount:0,
    chessBoard:[
        1,2,3,4,5,6,7,8,9
    ]

}
const getter = {
}
const actions = {
    move(context){
        context.commit("MOVE")
    },
}
const mutations = {
    MOVE(state){
        state.stepsCount++
    },
    CHANGEBOARD(state,value){
        state.chessBoard[value.index] = value.chess
    },
    SETORDER(state,value){
        state.first = value
    }
}


export default new Vuex.Store({
	actions,
	mutations,
	state,
    getter
})